import axios from 'axios'

const url = '/api'

class OfferService {
    static async CreateOffer(offerBody) {
        const response = await axios.post(url + '/offers', offerBody)
        return response.data
    }

    static async GetOffer(offerId) {
        const response = await axios.get(url + '/offers/' + offerId)
        return response.data
    }

    static async GetHomeworks(offerId) {
        const response = await axios.get(url + '/offers/' + offerId + '/homeworks')
        return response.data
    }

    static async DeleteOffer(offerId) {
        const response = await axios.delete(url + '/offers/' + offerId)
        return response.data
    }

    static async GetStudents(offerId) {
        const response = await axios.get(url + '/offers/' + offerId + '/students')
        return response.data
    }

    static async UnenrollStudents(offerId, students) {        
        const response = await axios.delete(url + '/offers/' + offerId + '/unenroll', students)
        return response.data
    }
}

export default OfferService;