<template>
  <div>
    <v-row>
      <v-col cols="4">
        <v-card raised shaped dark color="primary">
          <font size="+1">
            <v-row class="text-left pa-3 d-flex flex-column">
              <div class="mb-2">
                <v-icon class="mx-3">mdi-account</v-icon>
                <span class="font-weight-light">Name:</span>
                <strong> {{ student.name }}</strong>
              </div>
              <div>
                <v-icon class="mx-3">mdi-medal</v-icon>
                <span class="font-weight-light">Ranking:</span>
                <strong :class="{ 'green--text': successfullRanking }">
                  {{ ranking.rank }} of {{ ranking.size }}</strong
                >
              </div>
            </v-row>
          </font>
        </v-card>
      </v-col>
      <v-col cols="4">
        <v-card raised shaped dark color="primary">
          <font size="+1">
            <v-row class="text-left pa-3 d-flex flex-column">
              <div class="mb-2">
                <v-icon class="mx-3">mdi-book</v-icon>
                <span class="font-weight-light"
                  >Homework Completion Ratio:</span
                >
                <strong :class="{ 'green--text': successfullAssignmentRatio }">
                  {{ assignmentRatio.completed }} of
                  {{ assignmentRatio.all }}
                </strong>
              </div>
              <div>
                <v-icon class="mx-3">mdi-calculator</v-icon>
                <span class="font-weight-light">Homework Average:</span>
                <strong :class="{ 'green--text': succesfullAverage }">
                  {{ average }} %</strong
                >
              </div>
            </v-row>
          </font>
        </v-card>
      </v-col>
      <v-col cols="4">
        <v-card raised shaped dark color="primary">
          <v-card-title> </v-card-title>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        lg="6"
        md="4"
        v-for="(enrolled, index) in enrolleds"
        :key="index"
      >
        <v-card raised shaped>
          <v-card-title class="title text-left">
            <v-row class="ml-3">
              <v-col cols="12">
                <v-icon class="mr-3">mdi-book</v-icon>
                {{ enrolled.course }}
              </v-col>
              <v-col cols="12">
                <v-icon class="mr-3">mdi-account</v-icon>
                {{ enrolled.teacher }}
              </v-col>
              <v-col cols="12">
                <v-icon class="mr-3">mdi-calculator</v-icon>
                {{ enrolled.average }} %
              </v-col>
            </v-row>
          </v-card-title>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import StudentService from "../services/StudentService";
import CourseService from "../services/CourseService";
import TeacherService from "../services/TeacherService";
import OfferService from "../services/OfferService";

export default {
  data() {
    return {
      enrolleds: Array,
      studentId: Number,
      student: Object,
      average: Number,
      ranking: Number,
      assignmentRatio: Object,
      succesfullAverage: false,
      successfullRanking: false,
      successfullAssignmentRatio: false
    };
  },
  created() {
    this.studentId = this.$route.params.studentId;

    StudentService.GetStudent(this.studentId).then(student => {
      this.student = student;
    });

    StudentService.GetAllAverage(this.studentId).then(average => {
      if (average.average >= 85) {
        this.succesfullAverage = true;
      }
      this.average = average.average;
    });

    StudentService.GetStudentRanking(this.studentId).then(ranking => {
      if (ranking.rank < ranking.size * 0.1 || ranking.rank < 10) {
        this.successfullRanking = true;
      }
      this.ranking = ranking;
    });

    StudentService.GetStudentAssignmentRatio(this.studentId).then(
      assignmentRatio => {
        if (assignmentRatio.completed / assignmentRatio.all >= 0.8) {
          this.successfullAssignmentRatio = true;
        }
        this.assignmentRatio = assignmentRatio;
      }
    );

    StudentService.GetEnrolleds(this.studentId).then(enrolleds => {
      this.enrolleds = enrolleds;
      for (let i = 0; i < enrolleds.length; i++) {
        const el = enrolleds[i];
        CourseService.GetCourseById(el.courseId).then(course => {
          el.course = course.name;
          this.enrolleds.splice(i, 1, el);
        });

        StudentService.GetEnrolledAverage(this.studentId, el.id).then(
          enrolledAverage => {
            el.average = enrolledAverage.average;
            this.enrolleds.splice(i, 1, el);
          }
        );

        OfferService.GetOffer(el.offerId).then(offer => {
          TeacherService.GetTeacherById(offer.teacherId).then(teacher => {
            el.teacher = teacher.name;
            this.enrolleds.splice(i, 1, el);
          });
        });
      }
    });
  }
};
</script>

